const mokit = require('mokit-plugin').mokit;
const Class = mokit.Class;
const EventEmitter = mokit.EventEmitter;

const SEPARATOR = '#!';
const ROOT_PATH = '/';

/**
 *  has 
 */
const HashDriver = new Class({
  $name: 'HashDriver',
  $extends: EventEmitter,

  /**
   * 
   * @param {Object} router 
   * @returns {void} 
   */
  constructor: function (router) {
    this.$super();
    this.router = router;
    window.addEventListener('hashchange', function () {
      this._onChange();
    }.bind(this));
  },

  /**
   * 
   * @returns {string} 
   */
  get: function () {
    return location.hash.split(SEPARATOR)[1] || ROOT_PATH;
  },

  /**
   * 
   * @param {string} path 
   * @returns {void} 
   */
  set: function (path) {
    path = path || ROOT_PATH;
    location.hash = SEPARATOR + path;
  },

  /**
   * 
   * @param {string} path 
   * @returns {void} 
   */
  _onChange: function (path) {
    path = path || this.get() || '';
    if (path[0] != ROOT_PATH) path = ROOT_PATH + path;
    this.emit('changed', path);
  }

});

module.exports = HashDriver;


